import * as React from 'react';
import './App.css';
import ReactMarkdown from 'react-markdown'
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Container from '@mui/material/Container';
import Pagination from '@mui/material/Pagination';
import '@fontsource/roboto/400.css';

function App() {
  let [page, setPage] = React.useState(1)

  return (
    <>
      <Container maxWidth="sm">
        <Paper style={{ width: "100%" }}>
          <Typography pt={1} style={{ textAlign: "center" }} component="h1" variant="h2">Education and AI</Typography>

          <Typography pt={1} style={{ textAlign: "center" }} component="h2" variant="h5">By Stephen R. Foster, Ph.D. </Typography>

          <Box p={1} pl={5} >
            <Pages page={page} />
          </Box>
        </Paper>
      </Container>
        <Paper
          sx={{
            borderTop: "1px solid #000",
            marginTop: "auto",
            p: 2,
            position: "fixed",
            bottom: 0,
            width: "100%",
          }}
          component="footer"
      >
        <Box style={{margin: "auto", width: 400}}>
          <Pagination count={10}
            page={page}
            onChange={(e, p) => setPage(p)}
          />
        </Box>
      </Paper>
    </>
  );
}

function Pages({ page}) {

  return <>
    <ReactMarkdown>{paginate(fullText,page)}</ReactMarkdown>
  </>
}

let paginate = (text, page) => {
  let charsPerPage = 1000

  let start = (page - 1) * charsPerPage 
  let end = start + charsPerPage

  let startFuzz = 0 
  let endFuzz = 0 

  if (start > 0) {
    let startingChar = text[start + startFuzz]
    while (startingChar && !canEndPage(startingChar)) {
      startFuzz += 1
      startingChar = text[start + startFuzz]
    }
  }

  let endingChar = text[end + endFuzz]
  while (endingChar && !canEndPage(endingChar)) {  
    endFuzz += 1
    endingChar = text[end + endFuzz]
  }

  console.log(canEndPage("*"))

  return text.substring(start + startFuzz, end + endFuzz)
}

let canEndPage = (char) => {
  return char == "\n"
}

let fullText = `
### Part 1

#### 1.

The impact of AI-assisted writing is best understood in context of two other famous technological arrivals  of machines in general and of computers in particular.

Once upon a time, as the story goes, labor was something done exclusively by human hands.  Then came the machines  not all of a sudden, but pretty quickly once they got going.  The Luddites of the early 1800s did try to resist, managing to burn down a few factories.  But in the end, the machines won.  

Next (after much economic upheaval and a couple of big wars) came a very special kind of machine called the computer.  Unlike your run-of-the-mill machine, the computer could do something very important  something that had previously been a human-only form of labor: namely, math.  

To make a long story short, it turned out that a lot of really cool stuff actually boiled down to really fast math  everything from putting pixels on your screen, to reading the mouse and keyboard, to making the internet work, to *Fortnite*.  Bit by bit (byte by byte), the computers won.

Now, AI has arrived.  And by arrived  I mean that AI can, at times, perform what was once believed to be another human-only kind of labor: namely, writing.  AI-writing isnt perfect, and it cant write everything  but admit it, dear reader: 

> *You know in your heart that some parts of this very text might have been AI-generated, and you can never be sure exactly which ones.*

Even if I could somehow convince you I wrote this myself (Look, Ma!  No ChatGPT!), three inconvenient truths remain:

* Much writing can be automated.
* New types of automation tend to change the nature of work.
* When work changes, systems of education must adapt.

Lately, as an educator and writer (of much software and even a few books), Ive begun using AI in my lectures and professional writing.  The more it increases my productivity, the more I find myself mulling certain questions as I lay awake at night.  Who will lose their jobs?  If writing can be automated, what else can?  And how must education adapt?  

What follows are the answers Ive found, prepared in a way that I hope will be useful to other writers, educators, students, and even just the average worrier.  Its a wild world.

#### 2. 

This is a living document, as any meaningful text about a moving target must inevitably be.  Let me demonstrate this aliveness by introducing what I call a benchmark. Since this is the first one, Ill try to make it fun (though admittedly very subjective).

*Benchmark 1:* Write a Novel
* Goal: Get an AI to write a novel that Id actually want to read. 
* Models Tested:  GPT 3.5 and GPT 4.0
* Result: FAILED (as of Jul 26, 2023)

Ill admit, this benchmark might seem unfair at first glance.  Maybe I dont even like novels.  Maybe I have unreasonably high standards.  Who am I to appoint myself judge?  Look, these are valid concerns.   But if I may:

* On the one hand, Im not subjecting AI to any more subjectivity than human writers have been subjected to since the birth of writing (3400 BCE).  Writing is good if readers like it.
* On the other hand, Im not publishing these benchmarks in hopes that youll accept my results; rather, Im asking you to evaluate the benchmarks for yourself.

In other words, when you see a benchmark, if youre so inclined, please take a moment to open up your favorite AI-writing tool and use whatever clever prompting tricks youd like.  In particular, are your results different from mine?  Partly, this is for you (dear reader)  much like the exercises in a mathematics textbook, serving to imbue you with new intuitions about the subject.

But partly, its for me and for your fellow readers: We need your help maintaining this living document.  AI-writing tools and the techniques for prompting them are advancing.  So please get in touch if you ever find that your results differ from mine.  All you have to do is send me the output of your awesome AI-written novel along with a prompting process I can replicate.  If your novel (or one that pops out from your process) seems enjoyable to me, Ill update the benchmark, rewrite this section, and make you a co-author.  Pinky swear! 

Well look at a lot of benchmarks in the coming pages  some easy, some hard, some passed, some failed.  The reason I started with the novel-writing benchmark is that well use it momentarily to illustrate something important about the underlying technology of AI-writing  namely that the number of words GPT 4.0 can think about is 8,000.  For comparison, the first Harry Potter novel is about 77,000.  This means that the moment an AI tool writes 8000 words, it begins to forget its own words.

This is what makes the novel-writing benchmark so interesting  can the 8000-word context window be circumvented?  Optimists would point out here that JK Rowling likely doesnt keep every previous word in mind while she writes new ones.  Thus, perhaps a clever prompter could simulate a human writing process by continually supplying the AI with a running 500-word summary-thus-far, while also asking it to generate 7,500 words.  Do this 10 times and bam!  you have a novel!

If you get good results with this methodology, please get in touch, so I can update the benchmark.  But please know: Ive poured many hours into this problem, tried various such methodologies, and generated nothing but trash.  (More on this later  because sometimes even trash can be instructive.)

Ill close this section by mentioning a strange internet phenomenon Ive noticed:

* YouTube is filling up with videos about How to Write a Book with ChatGPT? 
* Everything Ive generated with these techniques sucks.

Again, maybe its me.  Or maybe (just maybe) the labor of book-writing cant be fully automated.  My current assumption is that it wont be for a long time.  If Im wrong, then two things will become true:

* Ill have to re-write much of what follows.
* I wont be doing the work.

#### 3.

Here we go again...
  `

export default App;
